@use 'variables';

:host {
  background: #eeeeee;
  height: 100%;
}

.settings-panel {
  display: flex;
  flex-direction: column;
  row-gap: 5px;
  position: sticky;
  top: variables.$top-panel-height + (2 * variables.$top-panel-padding);
  left: 0;
  width: variables.$settings-panel-width;
  padding: 5px;
  z-index: 1;
}

.settings-panel__map-id-container {
  display: flex;
  flex-direction: column;
  gap: 5px;
  align-items: center;
  padding: 5px;
}

.settings-panel__map-id-label {
  font-size: 1.5em;
  font-weight: bold;
}

.settings-panel__map-id-filename-container {
  display: table-cell;
  vertical-align: middle;
  gap: 5px;
}

.settings-panel__map-id-filename {
  font-family: 'Courier New';
  font-weight: bold;
}

.settings-panel__settings-main-header {
  font-size: 1.5em;
  font-weight: bold;
  text-align: center;
  margin: 0;
}

.settings-panel__settings-container-header {
  font-size: 1.17em;
  font-weight: bold;
  text-align: center;
  margin: 0;
}

.settings-panel__settings-container {
  display: grid;
  align-items: center;
  row-gap: 5px;
}

.settings-panel__general-settings-container {
  @extend .settings-panel__settings-container;
  grid-template:
    "minecraft-version-label minecraft-version-select        minecraft-version-select minecraft-version-select     " auto
    "number-of-maps-label    number-of-maps-horizontal-input number-of-maps-x         number-of-maps-vertical-input" auto
    / 50% 20% 10% 20%;
}

.settings-panel__minecraft-version-label {
  grid-area: minecraft-version-label;
}

.settings-panel__minecraft-version-select {
  grid-area: minecraft-version-select;
}

.settings-panel__number-of-maps-label {
  grid-area: number-of-maps-label;
}

.settings-panel__number-of-maps-horizontal-input {
  grid-area: number-of-maps-horizontal-input;
}

.settings-panel__number-of-maps-x {
  grid-area: number-of-maps-x;
  text-align: center;
}

.settings-panel__number-of-maps-vertical-input {
  grid-area: number-of-maps-vertical-input;
}

.settings-panel__preprocess-settings-container {
  @extend .settings-panel__settings-container;
  grid-template:
    "resize-label             resize-select           " auto
    "resize-quality-label     resize-quality-select   " auto
    "background-label         background-select       " auto
    / 50% 50%;
}

.settings-panel__resize-label {
  grid-area: resize-label;
}

.settings-panel__resize-select {
  grid-area: resize-select;
}

.settings-panel__resize-quality-label {
  grid-area: resize-quality-label;
}

.settings-panel__resize-quality-select {
  grid-area: resize-quality-select;
}

.settings-panel__background-label {
  grid-area: background-label;
}

.settings-panel__background-select {
  grid-area: background-select;
}

.settings-panel__reduce-colors-settings-container {
  @extend .settings-panel__settings-container;
  grid-template:
    "color-difference-label       color-difference-select      color-difference-select     " auto
    "dithering-label              dithering-select             dithering-select            " auto
    "transparency-label           transparency-label           transparency-number-input   " auto
    "transparency-range-container transparency-range-container transparency-range-container" auto
    / 50% 30% 20%;
}

.settings-panel__color-difference-label {
  grid-area: color-difference-label;
}

.settings-panel__color-difference-select {
  grid-area: color-difference-select;
}

.settings-panelb__dithering-label {
  grid-area: dithering-label;
}

.settings-panel__dithering-select {
  grid-area: dithering-select;
}

.settings-panel__transparency-label {
  grid-area: transparency-label;
}

.settings-panel__transparency-number-input {
  grid-area: transparency-number-input;
}

.settings-panel__transparency-range-container {
  grid-area: transparency-range-container;
  display: flex;
  justify-content: space-between;
}

.settings-panel__transparency-min-range-label {
  margin-right: 10px;
}

.settings-panel__transparency-range-input {
  flex-grow: 1;
}

.settings-panel__transparency-max-range-label {
  margin-left: 10px;
}

.settings-panel__create-file-settings-container {
  @extend .settings-panel__settings-container;
  grid-template:
    "auto-download-label auto-download-select" auto
    / 50% 50%;
}

.settings-panel__auto-download-label {
  grid-area: auto-download-label;
}

.settings-panel__auto-download-select {
  grid-area: auto-download-select;
}

settings-tooltip a {
  font-weight: bold;
}

settings-tooltip ul {
  margin-bottom: 0;
}